.exp-cards {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap : 10px;
    margin-left: 0;
    padding-inline-start: 0px;
}

.exp-cards  > * {
    border: 1px solid black;
    box-shadow: 5px -5px 4px grey;
    border-radius: 8px;
    list-style: none;
    padding: 10px;
}

li:hover  {
    box-shadow: 5px -5px 4px black;
    background-color: blueviolet;
}