.image-container {
    border: 3px solid black;
    /* box-shadow: 5px -5px 5px grey; */
    border-radius: 50%;
    height: 10em;
}

.image-container:hover {
    box-shadow: 5px -5px 5px grey;
    animation: mymove 5s infinite;
}

@keyframes mymove {
    50% {transform: rotate(360deg);}
  }


  .left-col {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }

  .left-col > * {
    margin-bottom: 0;
    margin-top: 0;
    color: black;
  }

  .left-col > h2 {
    font-family: cursive;
  }

  .left-col > * {
    align-self: center;
    margin-left: 10px;
  }